.focused .hs-input+.hs-reset:before {
    -webkit-transform: scale(0) rotate(180deg);
    -ms-transform: scale(0) rotate(180deg);
    -o-transform: scale(0) rotate(180deg);
    transform: scale(0) rotate(180deg);
}

.focused .hs-input+.hs-reset:after {
